<template>
    <div class="add-product">
        <Row>
            <Col span="12">
                <div class="choose-type">
                    产品编号：{{ productData.serverCode }}
                </div>
            </Col>
            <!-- <Col span="12" style="text-align: right;">
                <Button @click="edit()">编辑</Button>
            </Col> -->
        </Row>
        <div class="white-bg">
            <!-- 运输详情 -->
            <div class="add-site add-site-detail" v-if="type === 'transport'">
                <Row class="display-line" style="margin-top: 0;">
                    <Col span="3">
                        <div class="name">路段展示:</div>
                    </Col>
                    <Col span="21" class="line">
                        {{ productData.departurePort && productData.departurePort.split(';').join('') }}
                        <svg class="iconfont arrow" aria-hidden="true">
                            <use xlink:href="#icon-andaotongcaidanICON_jiantou"></use>
                        </svg>
                        {{ productData.destinationPort && productData.destinationPort.split(';').join('') }}
                    </Col>
                </Row>
                <Row class="parameter">
                    <Col span="3">
                        <div class="name">参数:</div>
                    </Col>
                    <Col span="21">
                        <Row>
                            <Col span="5">
                                运输方式：{{ productData.trspMode }}
                            </Col>
                            <Col span="5">
                                运输类型：{{ productData.trspType }}
                            </Col>
                            <Col span="5">
                                物流类型：{{ productData.logisticsType }}
                            </Col>
                            <Col span="5">
                                集装箱来源：{{ productData.contaSource }}
                            </Col>
                            <Col span="5" style="margin-top: 16px;">
                                货物类型：{{ productData.cargoType }}
                            </Col>
                            <Col span="5" style="margin-top: 16px;">
                                集装箱尺寸类型：{{ productData.contaSizeType }}
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </div>
            <!-- 场所详情 -->
            <div class="add-site add-site-detail" v-if="type === 'site'">
                <Row>
                    <Col span="3">
                        <div class="name">场所：</div>
                    </Col>
                    <Col span="5">
                        国家城市：{{ productData.sttnCode && productData.sttnCode.split(';').slice(0, productData.sttnCode.split(';').length - 1).join('') }}
                    </Col>
                    <Col span="5">
                        场所名称：{{ productData.sttnCode && productData.sttnCode.split(';').pop() }}
                    </Col>
                </Row>
                <Row class="parameter">
                    <Col span="3">
                        <div class="name">参数:</div>
                    </Col>
                    <Col span="5">
                        运输类型：{{ productData.trspType }}
                    </Col>
                    <Col span="5">
                        物流类型：{{ productData.logisticsType }}
                    </Col>
                </Row>
            </div>
            <!-- 关务详情 -->
            <div class="add-site add-site-detail" v-if="type === 'customs'">
                <Row>
                    <Col span="4">
                        报关类型：进口报关
                    </Col>
                    <Col span="4">
                        国家地区：中国
                    </Col>
                    <Col span="4">
                        城市：重庆
                    </Col>
                </Row>
                <div class="gq gq-detail" v-show="gqShow">
                    <span v-for="(item, index) in 20" :key="index">item</span>
                </div>
            </div>
            <!-- 综保区详情 -->
            <div class="add-site add-site-detail" v-if="type === 'cpa'">
                <Row>
                    <Col span="4">
                        国家地区：中国
                    </Col>
                    <Col span="10">
                        综保区名称：重庆两江综保区
                    </Col>
                </Row>
            </div>
        </div>
        <div class="white-bg middle-white-bg">
            <div class="product-list">
                <div class="product-content only-one" v-if="productData">
                    <div :class="['scene-btns', { spread: isSceneSpread }]">
                        <adt-tab-btns :titleNames="sceneTitleNames" :isSmallBtn="true" :btnIndex="sceneTabIndex" @handleBtn="handleSceneBtn" class="scene-tab"></adt-tab-btns>
                        <div class="show-all-scene" @click="isSceneSpread = !isSceneSpread">{{`${!isSceneSpread ? '展开' : '收起'}所有 >`}}</div>
                    </div>
                    <adt-tab-btns :titleNames="titleNames" :isUnderline="true" :btnIndex="tabIndex" @handleBtn="handleBtn" :width="250" :height="40" class="task-tab"></adt-tab-btns>
                    <div class="basic-item" v-show="isBasicTask">
                        <Row class="item-container">
                            <Col span="10" class="item">
                                基础服务总费用：{{ productData.sceneQueryRes[sceneTabIndex - 1].baseTaskQueryRes[0].taskCost }}{{ productData.sceneQueryRes[sceneTabIndex - 1].baseTaskQueryRes[0].curr }}/{{ (productData.trspType === 'C' || productData.trspType === '集装箱')  ? 'TEU' : 'KG' }}
                            </Col>
                            <Col span="14" class="item">
                                基础服务总时长：{{ productData.sceneQueryRes[sceneTabIndex - 1].baseTaskQueryRes[0].serverDuration }} {{ productData.sceneQueryRes[sceneTabIndex - 1].baseTaskQueryRes[0].serverDurationUnit }}
                            </Col>
                        </Row>
                        <adt-task-content :basicTask="productData.sceneQueryRes[sceneTabIndex - 1].baseTaskQueryRes" :isAdditionalTasks="false" :showPriceAndTime="false" />
                    </div>
                    <div class="additional-item" v-show="!isBasicTask">
                        <h6>附加任务内容:</h6>
                        <div class="task-container task-container-detail">
                            <!-- 详情可以一行放2个 -->
                            <div class="task-item" v-for="(additionalTask, key) in productData.sceneQueryRes[sceneTabIndex - 1].affixTaskQueryRes" :key="key">
                                <div class="task-name">{{ additionalTask.taskName }}</div>
                                <div class="cost">
                                    费用：{{ additionalTask.taskCost }}{{ additionalTask.curr }}/{{ (productData.trspType === 'C' || productData.trspType === '集装箱')  ? 'TEU' : 'KG' }}
                                </div>
                                <div class="time">
                                    时长：{{ additionalTask.serverDuration }}{{ additionalTask.serverDurationUnit }}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="public-component-line thin-line"></div>
                    <Row class="set-dead-line">
                        <Col span="10">
                            产品起止日期： {{ timestampFormat(productData.serverStartTime) }} -- {{ timestampFormat(productData.serverEndTime) }}
                        </Col>
                        <Col span="14">
                            产品报价截止时间前<span style="display: inline-block; margin: 0 16px;">{{ productData.serverRemindDuration }}</span>天开始提醒
                        </Col>
                    </Row>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import AdtTaskContent from '@/components/adt-task-content/Index'
    import { apiTransportQueryDetail, apiStationQueryDetail } from '@/api/productEditor' // eslint-disable-line
    import { timestampFormat } from '@/libs/utils'
    export default {
        components: {
            AdtTaskContent
        },
        props: ['type', 'serverCode'],
        data () {
            return {
                isModalShow: false,
                isSceneModalShow: false,
                sceneTabIndex: 1,
                tabIndex: 1,
                sceneTitleNames: [],
                titleNames: ['基础任务费用时长', '附加任务费用时长'],
                gqShow: true,
                isBasicTask: true,
                // 展开所有场景
                isSceneSpread: false,
                productData: ''
            }
        },
        created () {
            this.getData()
        },
        methods: {
            // 格式化时间
            timestampFormat (timeStamp) {
                return timestampFormat(timeStamp, true)
            },
            getData () {
                if (this.type === 'transport') {}
                switch (this.type) {
                    case 'transport':
                        apiTransportQueryDetail({ serverCode: this.serverCode }).then(({ data }) => {
                            this.productData = data
                            for (const scene of this.productData.sceneQueryRes) {
                                this.sceneTitleNames.push(scene.sceneName)
                            }
                        })
                        break
                    case 'site':
                        apiStationQueryDetail({ serverCode: this.serverCode }).then(({ data }) => {
                            this.productData = data
                            for (const scene of this.productData.sceneQueryRes) {
                                this.sceneTitleNames.push(scene.sceneName)
                            }
                        })
                }
            },
            handleSceneBtn (index) {
                this.sceneTabIndex = index + 1
            },
            handleBtn (index) {
                this.tabIndex = index + 1
                // 基础任务
                if (index === 0) {
                    this.isBasicTask = true
                } else {
                    // 附加任务
                    this.isBasicTask = false
                }
            },
            edit () {
                this.$router.push(`/product-editor/edit-product?type=${this.type}`)
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import './style.scss';
</style>
